import { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import "../css/detail-page.css";
import axios from "axios";

function DetailPage(props) {
  let { id } = useParams();
  const [title, setTitle] = useState(props.detailBoard.title);
  const [skillStack, setSkillStack] = useState(props.detailBoard.skill_stack);
  const [workField, setWorkField] = useState(props.detailBoard.work_field);
  const [maxPeople, setMaxPeople] = useState(props.detailBoard.max_people);
  const [endDate, setEndDate] = useState(props.detailBoard.end_date);
  const [content, setContent] = useState(props.detailBoard.content);
  useEffect(() => {
    // 글 내용 불러오기
    axios.get(`/api/board/detail/${id}`).then((res) => {
      props.getDetailBoard(res.data);
      setTitle(res.data.title);
      setSkillStack(res.data.skill_stack);
      setWorkField(res.data.work_field);
      setMaxPeople(res.data.max_people);
      setEndDate(res.data.end_date);
      setContent(res.data.content);
    });
  }, []);
  return (
    <>
      <ul>
        <li>타이틀 {title}</li>
        <li>작성자 {props.detailBoard.id}</li>
        <li>이름 {props.detailBoard.name}</li>
        <li>작성일 {props.detailBoard.create_date}</li>
        <li>사용 언어 {skillStack}</li>
        <li>분야 {workField}</li>
        <li>정원 {maxPeople}</li>
        <li>예상 기간 {endDate}</li>
        <li>내용 {content}</li>
      </ul>
      <div>
        <button>
          <Link to={`/update/${id}`}>수정</Link>
        </button>
        <button
          onClick={() => {
            axios.get(`/api/board/delete?id=${id}`);
            alert("삭제 완료");
            window.location.href = "/";
          }}
        >
          삭제
        </button>
      </div>
    </>
  );
}

export default DetailPage;
